#progress-bar-outline {
    background-color: #F2EDE9;
    width: 100%;
    height: 4px;
    border-radius: 2px;
}

#progress-bar-inline {
    margin: 0;
    padding: 0;
    background-color: #d1cdc9;
    transition: width 1.5s;
    width: 0%;
    height: 4px;
    border-radius: 2px;
}