body{
    background-color: #fff9f5;
    color: #242424;
    font-family: 'PT Serif', serif;
    padding: 10px;
    font-size: 14pt;
}

h1{
    margin-bottom: -5px;
    text-align: center;
    font-size: 24pt;
}

h2{
    margin-top: 15px;
    text-align: center;
    font-size: 15pt;
}

span{
    margin-top: 30px;
}

a{
    color: #242424;
    transition: 0.3s;
}

a:hover{
    color: #fff9f5;
}

button{
    font-size: 16pt;
    font-family: 'PT Serif', serif;
    padding: 10px 20px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
    -webkit-appearance: none;
    border-style: solid;
    border-width: 2px;
    border-color: #e3dfda;
    background-color: #f5f0eb;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 0px 5px #e3dfda;
}

input[type="radio"] {
    -ms-transform: scale(1.5);
    -webkit-transform: scale(1.5);
    transform: scale(1.5);
}

button{
    margin-top: 40px;
}

.surveyQuestion {
    margin-top: 20px;
    font-family: 'Open Sans';
}

.exampleText {
    margin-top: 15px;
    margin-bottom: -20px;
}

.exampleText>p {
    font-family: 'Open Sans';
    margin-bottom: 0px;
}

.exampleText>div{
    font-family: var(--main-article-font);
    font-size: var(--main-article-size);
    padding: 15px;
    padding-left: 18px;
    margin-top: 10px;
    margin-bottom: 15px;
    box-shadow: 0px 0px 5px 0px #d1cdc9;
    border-radius: 10px;
}

.radioGroup{
    font-family: 'Open Sans';
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin-top: 10px;
}

.radioChoice{
    margin-left: 10px;
    margin-right: 20px;
    cursor: pointer;
}

.radioChoiceContainer{
    cursor: pointer;
    padding: 1.5px 0px 1.5px 0px;
}

#intro>p{
    font-family: 'Open Sans';
}

#questionContainer{
    margin-top: 30px;
}

#surveyContainer{
    display: flex;
    flex-direction: column;
}

#surveyContainer>span{
    font-family: 'Open Sans';
}